:root {
  --orange: #f87719;
  --black: #111;
  --second-black: #282828;
  --gray: #595959;
  --light-gray: #b7b7b7;
  --white: #f8f8f8;
  --white-smoke: #fff;
  --liner: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --revers-liner: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  --transition-dur: 250ms;
  --transition-func: cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  letter-spacing: .02em;
  color: var(--white);
  background-color: var(--black);
  overflow: overlay;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.second-section {
  margin-bottom: 40px;
}

.home-pg-third-section {
  margin-bottom: 32px;
}

@media screen and (min-width: 768) {
  .second-section {
    margin-bottom: 52px;
  }

  .home-pg-third-section {
    margin-bottom: 44px;
  }
}

@media screen and (min-width: 1280) {
  .second-section, .home-pg-third-section {
    margin-bottom: 80px;
  }
}

.main-container {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 320px) {
  .main-container {
    width: 280px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .main-container {
    width: 707px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 1280px) {
  .main-container {
    width: 1216px;
    margin: 0 auto;
  }
}







.film-card {
  width: 280px;
  height: 406px;
  border-radius: 5px;
  flex-grow: 0;
  flex-shrink: 0;
  display: block;
  position: relative;
  overflow: hidden;
}

.film-card:hover {
  cursor: url("Clap_cinema128x128.2ff6c25c.svg"), auto;
  outline: 1px solid #fff;
}

@media (min-width: 768px) {
  .film-card {
    width: 224px;
    height: 325px;
  }
}

@media (min-width: 1280px) {
  .film-card {
    width: 395px;
    height: 574px;
  }
}

.film-card__poster {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.film-card__wrapper {
  background: linear-gradient(rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 92.16%);
  flex-direction: column;
  padding: 12px;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@media (min-width: 1280px) {
  .film-card__wrapper {
    padding-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

.film-card__film-name {
  text-transform: uppercase;
  color: var(--white-smoke);
  margin: auto 0 6px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.67;
}

@media (min-width: 768px) {
  .film-card__film-name {
    margin-bottom: 0;
  }
}

@media (min-width: 1280px) {
  .film-card__film-name {
    margin-bottom: 4px;
    font-size: 20px;
    line-height: 1.15;
  }
}

.film-card__film-info {
  text-transform: capitalize;
  color: var(--light-gray);
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.67;
  display: flex;
}

@media (min-width: 1280px) {
  .film-card__film-info {
    font-size: 20px;
    line-height: 1.15;
  }
}

.film-card__raiting {
  column-gap: 4px;
  display: flex;
}

.raiting-star-img {
  width: 14px;
  height: 14px;
}

@media (min-width: 768px) {
  .raiting-star-img {
    width: 10px;
    height: 10px;
  }
}

@media (min-width: 1280px) {
  .raiting-star-img {
    width: 18px;
    height: 18px;
  }
}

.film-card__genre {
  max-width: 120px;
  display: inline-block;
}

@media (min-width: 768px) {
  .film-card__genre {
    width: auto;
    max-width: 87px;
  }
}

@media (min-width: 1280px) {
  .film-card__genre {
    width: auto;
    max-width: 170px;
  }
}

.film-card__genre-year-wrapper {
  align-items: center;
  column-gap: 4px;
  display: flex;
}

.film-card__year {
  flex-grow: 1;
}

.raiting-body {
  letter-spacing: 3.7px;
  font-size: 17px;
  position: relative;
}

@media (min-width: 768px) {
  .raiting-body {
    font-size: 12px;
  }
}

@media (min-width: 1280px) {
  .raiting-body {
    font-size: 22px;
  }
}

.raiting-body:before {
  content: "★★★★★";
  color: var(--white-smoke);
  line-height: 1;
  display: block;
}

.rating-active {
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.rating-active-color {
  content: "★★★★★";
  width: 100%;
  height: 100%;
  background: var(--revers-liner);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  line-height: 1;
  display: flex;
  top: 0;
  left: 0;
}

.rating-active-wrapper {
  display: flex;
}

.rating-items {
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.raiting-star-radio {
  height: 100%;
  opacity: 0;
  flex: 0 0 20%;
  margin: 0;
}





.catalog-section {
  width: 280px;
  flex-wrap: wrap;
  row-gap: 16px;
  margin: 0 auto;
  display: flex;
}

@media (min-width: 768px) {
  .catalog-section {
    width: 704px;
    gap: 28px 16px;
  }
}

@media (min-width: 1280px) {
  .catalog-section {
    width: 1217px;
    row-gap: 32px;
  }
}















@media screen and (max-width: 767px) {
  header {
    padding-left: 12px;
    padding-right: 12px;
  }

  .header-nav__list {
    display: none;
  }

  .header-nav-list__wrapper p {
    color: var(--light-gray);
  }

  .logo-wrap span {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  header {
    padding-left: 24px;
    padding-right: 24px;
  }

  .header-nav-list__wrapper p {
    display: none;
  }

  .header-nav__list {
    gap: 30px;
    display: flex;
  }
}

header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo-wrap {
  align-items: center;
  gap: 8px;
  display: flex;
}

.logo-wrap span {
  transition: color var(--transition-func) var(--transition-dur);
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
}

.logo-wrap span:hover {
  color: var(--orange);
}

.header-logo {
  width: 32px;
  height: 32px;
  border: 5px solid #00f;
}

.header-nav__item {
  color: var(--light-gray);
  transition: color var(--transition-func) var(--transition-dur);
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}

.header-nav__item:hover, :focus {
  color: var(--orange);
}





